// Archive Product

h1 {
  text-align: center;
  color: green;
  font-size: 30px;
}

.jurnal-menu {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  // align-items: center;
  p {
    margin: 10px;
    color: green;
  }
}

hr {
  margin: 40px 0px;
  color: white;
  border-top: 3px dashed lightgrey;
}

.container-archive-page {
  width: 100%;
  height: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 50px;
}

.product {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
}

.product img {
  width: 100%;
  height: auto;
  padding: 0px;
}

.price {
  font-size: 24px;
}
